
div.cards {
	text-align: center;
	width: 100%;
}

div.card {
  background: #00000060;
  margin: 8px;
  min-width: 300px;
  width: 300px;
  font-family: 'Rubik';
  display: inline-block;
  max-width: 300px;
  perspective: 1000;
  position: relative;
  transition: all 0.2s 0s ease-in;
  z-index: 1;
  animation: showing 0.2s forwards;
  border-radius: 4pt;
} 

.show {
	animation: showing 0.3s forwards;
}

@keyframes showing {
  0% {opacity: 0;}
  100% {opacity: 1;}
}

.hide {
	animation: hidening 0.2s forwards;
}

.hiden {
	opacity: 0;
}

@keyframes hidening {
  0% {opacity: 1;}
  100% {opacity: 0;}
}

div.card-click {
	cursor: pointer;
}

div.card .title-card-img {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  border-top-left-radius: 4pt;
  border-top-right-radius: 4pt;
} 
div.card .card__image-holder {
  padding-bottom: 55%;
  position: relative;
}
div.card .card__image-holder-game {
  padding-bottom: 45%;
}
div.card div.card-title {
  padding: 4px;
  position: relative;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  border-bottom-left-radius: 4pt;
  border-bottom-right-radius: 4pt;
}

div.card-title * {
	text-overflow: ellipsis;
	overflow: hidden;
	white-space: nowrap;
}

div.card div.card-title h2 {
    font-size: 24px;
    letter-spacing: -0.05em;
}

div.card div.card-description {
    position: relative;
    font-size: 14px;
    text-align: left;
    white-space: unset;
}
div.card div.card-flap {
	backdrop-filter: blur(9px);
	//min-height: 210px; // Всплывашка во всю карточку
    padding: 5pt;
    margin-top: 3pt;
    border-radius: 4pt;
    position: absolute;
    width: 225pt;
    opacity: 0;
	margin-top: -157pt;
	pointer-events: none;
	
	button {
		pointer-events: none;
	}
}
div.card div.card-flap-game {
	margin-top: -134pt;
}

div.card.show div.card-flap {
    background: rgba(26, 26, 26, 0.856);
	button {
		pointer-events: all;
	}
}

div.card-flap div.flap-buttons {
	display: flex;
    flex-direction: row-reverse;
}




div.card div.card-title a.toggle-info {
	border-radius: 32px;
	height: 32px;
	padding: 0;
	position: absolute;
	right: 15px;
	top: 10px;
	width: 32px;
}

div.card div.card-title a.toggle-info span {
	background: #ffffff;
	display: block;
	height: 2px;
	position: absolute;
	top: 16px;
	transition: all 0.2s 0s ease-out;
	width: 12px;
}

div.card div.card-title h2 {
	font-size: 24px;
	letter-spacing: -0.05em;
	margin: 0;
	padding: 0;
}

div.card div.card-title h2 small {
	display: block;
	font-size: 18px;
	letter-spacing: -0.025em;
}

div.card div.card-description {
	padding: 0 15px 10px;
	position: relative;
	font-size: 14px;
    word-wrap: break-word;
}

div.card div.card-actions {
	box-shadow: 0 2px 0px 0 rgba(0, 0, 0, 0.075);
	padding: 10px 15px 20px;
	text-align: center;
}

div.card div.card-flap {
	position: absolute;
    opacity: 0;
	transition: opacity 0.3s;
	pointer-events: none;
}

div.cards.showing div.card {
	opacity: 0.98;
	transform: scale(0.98);
}

div.card.show {
	transform: scale(1) !important;
}

div.card.show div.card-flap {
	opacity: 1 !important;
	transition: opacity 0.3s;
	pointer-events: all;
}

.button-flap {
	height: 30pt;
	margin-top: -6pt;

    pointer-events: all;
	cursor: pointer;
}
.button-card {
	width: 50pt;
	background: rgb(68, 68, 68);
	border: none;
	border-radius: 1pt;
	border-top-right-radius: 20pt;
	border-bottom-right-radius: 20pt;
}
.button-card:hover {
	background: rgb(58, 58, 58);
}
.button-card:active {
	background: rgb(48, 48, 48);
}

.button-game {
	border: 1.4px solid transparent;
	background-image: linear-gradient(to right, #e2e2e2, #a5a5a5);
	box-shadow: 0 0 0 100px rgb(61, 68, 95) inset;
	background-origin: border-box;

	border-radius: 5pt;
	img {
		height: 100%;
		width: 100%;
	}
}
.button-game:hover {
	background-image: linear-gradient(to right, #9c9c9c, #888888);
}
.button-game:active {
	background-image: linear-gradient(to right, #797979, #4b4b4b);
}

.img-to-mod {
	width: 30pt;
}
