div.card {
  background: rgba(0, 0, 0, 0.3764705882);
  margin: 8px;
  min-width: 300px;
  width: 300px;
  font-family: "Rubik";
  display: inline-block;
  max-width: 300px;
  perspective: 1000;
  position: relative;
  transition: all 0.2s 0s ease-in;
  z-index: 1;
  animation: showing 0.2s forwards;
  border-radius: 4pt;
}

.show {
  animation: showing 0.3s forwards;
}

@keyframes showing {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.hide {
  animation: hidening 0.2s forwards;
}

.hiden {
  opacity: 0;
}

@keyframes hidening {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
div.card-click {
