.centered {
    position: relative;
    z-index: 10;
    width: 100%;
    height: 70vh;
    display: grid;
    align-items: center;
    justify-content: center;
    text-align: center;
	font-family: 'Hack', monospace;
}


.not__found__page__error,
.not__found__page__link {
	text-align: center;
}

.not__found__page__error {
	margin-top: -35px;
	line-height: 30px;
	font-size: 2rem;
}

.not__found__page__link {
    color: rgb(228, 228, 228);
	margin-top: 5px;
	line-height: 20px;
	font-size: 1.4rem;
	text-decoration: none;
	cursor: pointer;
	
	&:hover {
		text-decoration: underline;
	}
}