
html {
    font-style: normal;
}

.main-about {
    position: relative;
    z-index: 1;
    border-radius: 10pt;
    margin: 10pt;
    padding: 10pt;
    text-align: left;
    font-size: 14pt;
    font-family: Arial, Helvetica, sans-serif;
}

article {
    background: rgba(0, 0, 0, 0.3);
    margin: 4pt;
    border-radius: 10pt;
    padding: 10pt;
}

h1, h2, h3, h4, h5, h6 {
    color: rgb(200, 200, 200);
}

h1 {
    font-size: 20pt;
    color: rgb(203, 203, 203);
}

h2 {
    font-size: 17pt;
    color: rgb(213, 213, 213);
}

h3 {
    font-size: 15pt;
    color: rgb(223, 223, 223);
}

h4 {
    font-size: 13pt;
    color: rgb(233, 233, 233);
}

h5 {
    font-size: 11pt;
}

h6 {
    font-size: 9pt;
}

i {
    font-style: italic;
}

b {
    font-weight: bold;
}


.ad-button-b {
    animation: 1.5s ease infinite alternate running shimmer;
    background: linear-gradient(90deg, #FFE27D 0%, #64E3FF 30%, #9192FF 85%);
    background-size: 200% 100%;
    border: none;
    border-radius: 6px;
    box-shadow: -2px -2px 10px rgba(255, 227, 126, 0.5), 2px 2px 10px rgba(144, 148, 255, 0.5);
    color: #170F1E;
    cursor: pointer;
    font-family: 'Inter', sans-serif;
    font-size: 16px;
    font-weight: 670;
    line-height: 24px;
    overflow: hidden;
    padding: 12px 24px;
    position: relative;
    text-decoration: none;
    transition: 0.2s;
    
    svg {
        left: -20px;
        opacity: 0.5;
        position: absolute;
        top: -2px;
        transition: 0.5s cubic-bezier(.5,-0.5,.5,1.5);
    }
        
    &:hover svg {
        opacity: 0.8;
        transform: translateX(50px) scale(1.5);
        }
        
    &:hover {
        transform: rotate(-3deg);
        }
        
    &:active {
        transform: scale(0.95) rotate(-3deg);
    }
}

@keyframes shimmer {
    to {
        background-size: 100% 100%;
        box-shadow: -2px -2px 6px rgba(255, 227, 126, 0.5), 2px 2px 6px rgba(144, 148, 255, 0.5);
    }
}
