
.mod-container {
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    max-width: 1000pt;
    background: rgba(0, 0, 0, 0.3);
    border-radius: 10pt;
    padding: 5pt;
    margin: 1;
}

@media (min-width: 700px) {
	.mod-container {
      display: flex;
	}
  }
  
@media (max-width: 699px) {
	.mod-container {
        display: block;
	}
}


.mod-dependence-list {
    background: rgba(2, 0, 12, 0.199);
    margin-top: 4pt;
    border-radius: 10pt;
    padding: 5pt;
}

.mod-dependence {
    background: rgba(26, 26, 26, 0.452);
    
    margin-top: 5pt;
    
    cursor: pointer;

    border-radius: 10pt;
    padding: 5pt;

    display: flex;

    vertical-align: middle;

    padding-top: auto;
    padding-bottom: auto;

    img {
        border-radius: 5pt;
        margin-right: 5pt;
        width: 35pt;
        height: 30pt;
    }

    h3 {
        vertical-align: middle;
    }

    div {
        height: 10%;
        vertical-align: middle;
    }
}
