.img-desc {
    max-width: 190pt;
    max-height: 200pt;
    width: auto;
    height: auto;
    position: relative;
    z-index: 10;
}

.box {
    width: 100%;
    position: relative;
    display: flex;
    z-index: 1;
}

.panel {
    pointer-events: none;
    position: fixed;
    margin: 10pt;
    width: 260pt;
    max-width: 92%;
    z-index: 10;
}

.main {
    padding-top: 80pt;    
    min-height: calc(100vh - 50pt);
    /* 80px header + 40px footer = 120px  */
}

#container {
    position: relative;
    transition: margin-left 0.4s, background-color 0.4s, opacity 0.1s;
    display: grid;
    border-radius: 5pt;
    padding: 5pt;
    background: rgba(27, 27, 27, 0.8);
    backdrop-filter: blur(5px);
    height: auto;
    max-width: 260pt;
    pointer-events: all;
    opacity: 99%;
    margin-bottom: 5pt;
  }
  
#container.moved {
  margin-left: -350px;
  background: black;
  opacity: 50%;
}

#panel-button {
    background: rgba(100, 100, 100, 0.644);
    position: relative;
    border: 0;
    border-radius: 17pt;
    padding: 2pt;
    opacity: 80%;
    pointer-events: all;
    cursor: pointer;
}



.button-img {
    width: 40pt;
}

.space-h {
    height: 4pt;
}

::-webkit-slider-thumb {
    color: #000;
}

::-webkit-scrollbar {
    width: 10px;
    background-color: #555555;
}

::-webkit-scrollbar-thumb {
    background-color: #adadad;
}

::-webkit-scrollbar-thumb:hover {
    background-color: #929292;
}

::-webkit-scrollbar-thumb:active {
    background-color: #8f8f8f;
}

.result-search {
    border-radius: 3pt;
    display: inline;
    background-color: rgba(229, 255, 0, 0.411);
}


.steam-link, .tag-link, .tag-link-green, .tag-link-blue, .tag-link-red, .tag-link-orange {
    background-color: rgb(61, 61, 61);
    border-radius: 4pt;
    padding: 1pt;
    padding-left: 3pt;
    padding-right: 3pt;
    color: rgb(230, 238, 255);
    font-style: normal;
    text-decoration: inherit;
}

.tag-link-green {
    background-color: green;
}

.tag-link-blue {
    background-color: blue;
}

.tag-link-red {
    background-color: red;
}

.tag-link-orange {
    background-color: rgb(194, 126, 0);
}


.game-selector-in-menu {
    margin: 0;
    margin-top: 3pt;
    padding: 4pt;
    border-radius: 3pt;
    background-color: #000000bb;

    #currectGames {
        background-color: #55555570;
        border-radius: 3pt;
        padding: 2pt;
        display: flex;
        align-items: center;
        #currectGamesReset {
            border-radius: 5pt;
            
            border: 1.4px solid transparent;
            background-image: linear-gradient(to right, #a8a8a8, #818181);
            box-shadow: 0 0 0 100px rgb(32, 34, 61) inset;
            background-origin: border-box;

            &:hover {
                background-image: linear-gradient(to right, #919191, #6d6d6d);
            }
            &:active {
                background-image: linear-gradient(to right, #757575, #575757);
            }

            padding: 0pt;
            margin-right: 3pt;
            img {
                margin: -4pt;
                height: 30pt;
            }
        }
    }
}
